module.exports = " <div id=app> <div class=header> <h1>ToDo</h1> </div> <div class=main> <div class=left> <div v-bind:class=\"[listtitle, alltasks, isAllTaskActive ? active : '']\" v-on:click=allTask> 所有任务 ({{num}}) </div> <div class=list-title>分类列表</div> <div id=listcontent class=list> <ul> <li v-for=\"cate in cates\"> <h2 v-on:click=cateClick(cate) v-bind:class=\"{'active':cate.isActive}\"><i class=\"fa fa-folder-open\"></i><span>{{cate.name}}</span> ({{cate.childCate.length}})</h2> <ul> <li v-for=\"child in cate.childCate\"> <h3 v-bind:class=\"{'active':child.isActive}\" v-on:click=childClick(child)><i class=\"fa fa-file-o\"></i><span>{{child.name}}</span> ({{child.tasks.length}})</h3> </li> </ul> </li> </ul> </div> <button class=add v-on:click=clickAddCate><i class=\"fa fa-plus\"></i>新增分类</button> </div> <div class=mid> <div class=status> <button id=all-tasks class=active v-on:click=allTasksClick()>所有</button> <button id=unfinish-tasks class=\"\" v-on:click=unfinishTaksClick()>未完成</button> <button id=finished-tasks class=\"\" v-on:click=finishedTasksClick()>已完成</button> </div> <div id=task-list class=task-list> <div v-for=\"tasks in taskList\"> <div>{{tasks.date}}</div> <ul> <li v-for=\"task in tasks.tasks\" v-bind:class=\"{'active':task.isActive,'task-done':task.isFinished}\" v-on:click=taskClick(task)><i class=\"fa fa-check\" v-show=task.isFinished></i>{{task.name}}</li> </ul> </div> </div> <button class=\"add-task add\" v-on:click=addTask()><i class=\"fa fa-plus\"></i>新增任务</button> </div> <div class=right> <div class=title> <div class=todo-name v-show=!editing>{{currentTask.name}}</div> <div class=todo-name id=todoName v-show=editing><input type=text class=input-title placeholder=请输入标题 v-model=currentTask.name></div> <div class=manipulate> <a v-on:click=checkClick()><i class=\"fa fa-check-square-o\"></i></a> <a v-on:click=pencilClick()><i class=\"fa fa-pencil-square-o\"></i></a> </div> </div> <div class=task-date v-show=!editing> 任务日期：<span>{{currentTask.date}}</span> </div> <div class=task-date v-show=editing id=taskDate> 任务日期：<span><input type=date class=input-date v-model=currentTask.date></span> </div> <div class=content v-show=!editing> {{currentTask.content}} </div> <div class=content v-show=editing> <textarea class=textarea-content placeholder=请输入任务内容 id=textareaContent v-model=currentTask.content></textarea> </div> <div class=button-area v-show=editing> <span class=info></span> <button class=save v-on:click=saveClick()>保存</button> </div> </div> </div> <div class=cover v-show=isCover> <div class=modal> <div class=modal-head> 新增分类 </div> <div class=modal-body> 选择主分类： <select id=modal-select> <option>新增主分类</option> <option v-for=\"cate in cates\">{{cate.name}}</option> </select> <br> 新分类名称： <input id=newCateName type=text v-model=message> </div> <div class=modal-foot> <button v-on:click=cancel>取消</button> <button v-on:click=ok>确认</button> </div> </div> </div> </div> ";


